.correction {
  background-color: $white;
  color: $dark-blue-desaturarion;

  @media (min-width: $desktop-width) {
    padding-bottom: 23px;
  }
}

.correction__title {
  margin: 0 auto;
  margin-bottom: 43px;
  width: 280px;
  line-height: 30px;
  text-align: center;

  @media (min-width: $tablet-width) {
    margin-bottom: 67px;
    width: 450px;
    font-size: 18px;
  }
}

.correction__wrapper {
  padding-top: 20px;
  padding-bottom: 41px;
  background-color: $grey;

  @media (min-width: $tablet-width) {
    padding-top: 57px;
    padding-bottom: 54px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 auto;
    padding-bottom: 57px;
    padding-right: 60px;
    padding-left: 80px;
    width: 940px;
  }
}

.correction__center {
  @media (min-width: $tablet-width) {
    display: flex;
    column-gap: 45px;
    margin: 0 auto;
    width: 620px;
  }

  @media (min-width: $desktop-width) {
    column-gap: 90px;
    margin: 0 auto;
    width: 100%;
  }
}

.correction__image-wrapper {
  margin: 0 auto;
  margin-bottom: 42px;
  width: 280px;
  height: 374px;

  @media (min-width: $tablet-width) {
    margin: 0;
    width: 300px;
    height: 400px;
  }
}

.correction__image {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.correction__settings {
  margin: 0 auto;
  width: 280px;

  @media (min-width: $tablet-width) {
    margin: 0;
    width: 275px;
  }

  @media (min-width: $desktop-width) {
    width: 410px;
  }
}

.correction__parameters {
  display: flex;
  column-gap: 35px;
  margin: 0 auto;
  margin-bottom: 39px;
  width: 214px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.correction__parameter {
  display: block;
  position: relative;
  padding: 0;
  width: 48px;
  height: 44px;
  background-color: transparent;
  border: none;
  color: $dark-blue-desaturarion;
  opacity: 0.3;

  &--contrast .correction__icon {
    left: 15px;
  }

  &:hover {
    opacity: 0.5;
  }

  &:active,
  &--current {
    color: $pink;
    opacity: 1;
  }
}

.correction__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.correction__lines {
  margin-bottom: 73px;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    row-gap: 70px;
    padding-top: 20px;
    margin-bottom: 104px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 181px;
  }
}

.correction__line {
  position: relative;
  margin: 0 auto;
  width: 240px;
  height: 2px;
  background-color: $dark-blue-desaturarion;

  &:not(.correction__line--main) {
    display: none;
  }

  @media (min-width: $tablet-width) {
    margin: 0;
    margin-left: 57px;
    width: 202px;

    &--main {
      display: none;
    }

    &:not(.correction__line--main) {
      display: block;
    }

    // Иконки настроек

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: -53px;
      transform: translateY(-50%);
      width: 24px;
      height: 22px;
      background-image: url("../img/catalog/correction/crop.svg");
      background-repeat: no-repeat;
      background-position: center center;
    }

    &--tint:before {
      background-image: url("../img/catalog/correction/fill.svg");
    }

    &--contrast:before {
      background-image: url("../img/catalog/correction/contrast.svg");
      background-size: 32px 32px;
    }

    // Линия под настройками

    &:after {
      content: "";
      position: absolute;
      right: -18px;
      top: 35px;
      width: 275px;
      height: 1px;
      background-color: rgba(40, 54, 69, 0.1)
    }
  }

  @media(min-width: $tablet-width) {
    &--contrast:before {
      background-size: 16px 16px;
    }
  }

  @media (min-width: $desktop-width) {
    margin-left: 92px;
    width: 288px;

    &::before {
      left: -92px;
      width: 48px;
      height: 44px;
      background-size: 100% 100%;
    }

    &--contrast:before {
      background-size: 32px 32px;
    }

    &:after {
      right: -30px;
      width: 410px;
    }
  }
}

.correction__toggle {
  position: absolute;
  top: 50%;
  left: 193px;
  transform: translateY(-50%);
  padding: 0;
  width: 14px;
  height: 14px;
  background-color: $white;
  border: 2px solid $pink;
  border-radius: 50%;

  &:hover,
  &:active {
    background-color: $pink;
  }

  @media(min-width: $tablet-width) {
    .correction__line--size & {
      left: 79.2%;
    }

    .correction__line--tint & {
      left: 12.4%;
    }

    .correction__line--contrast & {
      left: 47.5%;
    }
  }
}

.correction__button-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 12px;

  @media (min-width: $tablet-width) {
    row-gap: 24px;
    margin: 0 auto;
    width: 260px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    justify-content: space-between;
    row-gap: 0;
    margin: 0;
    width: 410px;
  }
}

.correction__button {
  width: 100%;
  color: $pink;
  border-color: $pink;

  &--post:hover {
    background-color: $pink;
    color: $white;
  }

  &--post {
    &:hover {
      background-color: $pink;
      color: $white;
    }

    &:active {
      background-color: $pink;
      color: rgba(255, 255, 255, 0.3);
    }
  }


  &--reset {
    opacity: 0.1;
    color: $dark-blue-desaturarion;
    border-color: $dark-blue-desaturarion;

    &:hover {
      opacity: 0.3;
    }

    &:active {
      background-color: $dark-blue-desaturarion;
      color: rgba(255, 255, 255, 0.3);
    }
  }

  @media (min-width: $desktop-width) {
    width: auto;
    min-width: 183px;
  }
}
